.calculator {
  margin-top: 82px;
  background: url("./../img/background.svg");
  padding-top: 70px;
  padding-bottom: 70px;

  & .center {
    display: flex;
    align-items: center;
    justify-content: center;
  }
}

.calculator_form {
  background: #FFFFFF;
  box-shadow: 0px 10px 80px -30px rgba(74, 80, 237, 0.3);
  border-radius: 6px;
  max-width: 880px;
  padding-bottom: 30px;
  display: flex;
  flex-direction: column;
}

.calculator_title {
  font-family: 'Ubuntu', sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 30px;
  line-height: 100%;
  text-align: center;
  color: #11244B;
  padding: 30px 60px;
}

.calculator_wrapper {
  border-top: 1px solid #D0D0D0;
  padding: 30px 60px;
}

.calculator_question {
  font-family: 'Ubuntu', sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 21px;
  line-height: 100%;
  color: #11244B;
  width: 100%;
}

.calculator_answers-wrapper {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
}

.calculator_checkbox {
  display: none;
}

.calculator_answer {
  border: 1px solid #D0D0D0;
  box-sizing: border-box;
  border-radius: 6px;
  padding: 16px 20px;
  flex-basis: 48.6%;
  margin-top: 20px;
  display: flex;
  flex-direction: row;
  align-items: center;
  font-family: 'Ubuntu', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 90%;
  color: #11244B;

  &::before {
    content: "";
    width: 18px;
    height: 18px;
    border-radius: 50%;
    border: 1px solid #D0D0D0;
    margin-right: 15px;
    background: white;
  }

  &:hover {
    cursor: pointer;
    border-color: #4A50ED;
  }
}

.calculator_checkbox:checked + .calculator_answer::before {
  background: url("data:image/svg+xml,%0A%3Csvg width='10' height='10' viewBox='0 0 10 10' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Ccircle cx='5' cy='5' r='5' fill='%234A50ED'/%3E%3C/svg%3E%0A") center no-repeat;
}

.calculator_text-input {
  background: #F8F8F8;
  border: 1px solid #D9D9D9;
  box-sizing: border-box;
  border-radius: 4px;
  padding: 16px 20px;
  font-family: 'Ubuntu', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 120%;
  color: #939393;
  flex-basis: 48.6%;
  margin-top: 20px;

  &::placeholder {
    font-family: 'Ubuntu', sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 120%;
    color: #939393;
  }

  &.valid {
    background: url("data:image/svg+xml,%0A%3Csvg width='24' height='24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M9.646 14.818a.5.5 0 00.707 0l8.132-8.132A1 1 0 1119.9 8.1l-9.193 9.193a1 1 0 01-1.414 0l-4.95-4.95a1 1 0 111.414-1.414l3.89 3.89z' fill='%2315AF40'/%3E%3C/svg%3E") 98% center no-repeat, #f8f8f8;
  }
}

.calculator_submit {
  background: #15AF40;
  border-radius: 5px;
  font-family: 'Ubuntu', sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 18px;
  line-height: 140%;
  color: #FFFFFF;
  border: none;
  min-width: 280px;
  min-height: 54px;
  margin-right: auto;
  margin-left: auto;

  &:disabled {
    opacity: 0.7;
  }
}

.calculator_result {
  position: fixed;
  bottom: 0;
  right: 0;
  left: 0;
  min-height: 65px;
  display: none;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  background: #4A50ED;

  &.active {
    display: flex;
  }
}

.calculator_text {
  display: flex;
  flex-direction: column;
  font-family: 'Ubuntu', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 100%;
  color: #FFFFFF;

  & span {
    font-style: normal;
    font-weight: 500;
    font-size: 18px;
    line-height: 100%;
    margin-top: 5px;
  }

  &:not(:last-of-type) {
    margin-right: 30px;
  }
}

.valid-error {
  display: block;
  width: 100%;
  color: red;
  font-size: 11px;
  text-align: right;
}

@media(max-width: 767px) {
  .calculator {
    margin-top: 60px;
    padding-top: 40px;
    padding-bottom: 40px;
  }

  .calculator_form {
    padding-bottom: 22px;
  }

  .calculator_title {
    font-size: 24px;
    line-height: 115%;
    padding: 22px 15px;
  }

  .calculator_wrapper {
    padding: 22px 15px;
  }

  .calculator_question {
    font-size: 19px;
    line-height: 115%;
  }

  .calculator_answers-wrapper {
    display: flex;
    flex-direction: column;
  }

  .calculator_answer {
    margin-top: 10px;
  }

  .calculator_result {
    min-height: 62px;
  }

  .calculator_text {
    font-size: 13px;
  
    & span {
      font-size: 17px;
      margin-top: 54x;
    }
  }
}